.login__title {
  font-weight: 900;
  font-size: 24px;
  line-height: 1.2;
  text-align: center;
  color: #fff;
  margin: 0 0 5.67% 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .login__title {
    font-size: 20px;
    margin-bottom: 12.5vw;
  }
}